$font-family: 'Cherry Swash', cursive;
$font-size: 16px;
$line-height: 1.5;

html {
    font-size: 14px;
    box-sizing: border-box;
}

body {
    line-height: $line-height;
    font-family: $font-family;
    font-size: $font-size;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    // font-size: 100%;
    font-weight: 400;
}

a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;

    &:hover {
        color: seagreen;
    }

    // &:visited {
    //     color: inherit;
    // }
}

img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    padding: 10px 30px;
    box-sizing: border-box;
}

// code snippets
pre.highlight {
    padding: 8px;
    overflow: auto;
}
