@mixin hover-animation {
  transition: all .5s ease 0s;
  border-radius: 8px;
}


body.index {
  height: 100vh;
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center;     /* 垂直居中 */
  font-size: 12px;
  background: #2c3e50;
  font-family: 'Cherry Swash', cursive;
  color: #ecf0f1;

  & > div {
    height: 400px;
    width: 800px;
  }

  h1 {
    text-align: center;
    margin: 40px 0 40px;
    font-size: 60px;
    text-shadow: 2px 2px 4px #000000;
  }

  p {
    display: flex;
    justify-content: center;
    margin: 0 0 40px;
    font-size: 16px;
  }

  span {
    color: #2BD6B4;
  }
}

